<template>
    <div id="body">
        <div id="menu">
            <v-btn @click="toggleMenu()" class="mb-1" fab icon><Icon color="#01579b" :icon="['fa','list']"/></v-btn>
            <v-btn @click="selectData()" v-show="menu_btn" class="mb-1" fab><Icon :icon="['fa','folder']"/></v-btn>
            <v-btn @click="selectJson()" v-show="menu_btn" class="mb-1" fab><Icon :icon="['fa','file-upload']"/></v-btn>
            <v-btn @click="saveJson()" v-show="menu_btn" class="mb-1" fab><Icon :icon="['fa','download']"/></v-btn>
            <v-btn @click="addData()" dark color="green" class="mb-1" fab><Icon :icon="['fa','plus']"/></v-btn>
        </div>

        <div>
            <v-card width="250px" :style="{float:'right',padding:'1em',margin:'1px'}">
               <v-card-title>Add Features</v-card-title>
               <v-text-field outlined label="Feature Name" v-model="featureName"></v-text-field>
                <v-radio-group>
                   <div><v-chip outlined class="mx-1 mb-1">Undefined</v-chip> <input @click="featureSelect(1)" value="undefined" type="radio" name="feature" id="feature"></div>
                    <div><v-chip outlined class="mx-1 mb-1">Image</v-chip> <input @click="featureSelect(2)" value="image" type="radio" name="feature" id="feature"></div>
                     <div><v-chip outlined class="mx-1 mb-1">Label</v-chip> <input @click="featureSelect(3)" value="label" type="radio" name="feature" id="feature"></div>
                </v-radio-group>
                <v-card-actions>
                    <v-btn @click="addFeatures()" fab color="#01579b" dark small><Icon :icon="['fa','plus']"/></v-btn>
                    <v-chip>
                        Automatic Labeling 
                        <v-btn icon @click="automaticLabeling=!automaticLabeling">
                            <Icon v-if="automaticLabeling" color="green" :icon="['fa','record-vinyl']"/>
                            <Icon v-if="!automaticLabeling" color="red" :icon="['fa','record-vinyl']"/>
                            </v-btn>
                        </v-chip>
                </v-card-actions>  
            </v-card>
        </div>

        <div :style="{display:'flex',justifyContent:'center'}">

        <div>
            <v-chip color="white" outlined >Total Uploads</v-chip>  
        <v-progress-circular width="10" class="mx-2 mt-2" :size="100" :value="totalUploads" color="orange">
            {{totalUploads}}
        </v-progress-circular>
        </div>

        <div>
            <v-chip color="white" outlined >Total Labeled Uploads</v-chip>  
            <v-progress-circular width="10" class="mx-2 mt-2" :size="100" :value="totalLabeledUploads" color="orange">
                {{totalLabeledUploads}}
            </v-progress-circular>
        </div>

        </div>

        <div id="label_div">
            <v-chip outlined class="mx-1" color="white" v-for="(item,index) in labels" :key="index">
                {{item.name}}
                <v-btn icon fab @click="removeFeature(index)"><Icon color="orange" :icon="['fa','times']"/></v-btn>
                </v-chip>
        </div>

        <div>
            <input  accept="application/json" type="file" name="json" id="json" v-show="false">
            <input @change="loadTheImages()" accept="image/*" type="file" name="data_select" id="data_select" v-show="false" multiple>
        
            <div id="image_div" :style="{display:'flex',justifyContent:'center',alignItems:'center',marginBottom:'2px'}">
                <img :src="imageUrl" :style="{maxWidth:'350px',maxHeight:'350px'}">
            </div>

            <div v-if="!automaticLabeling" :style="{display:'flex',alignItems:'center',justifyContent:'center'}">
                <div :style="{width:'250px'}">
                    <v-text-field label="Image Label Number" hint="1,2,3,4,5|1,2,3,1,2,3"></v-text-field>
                </div>
            </div>

            <div :style="{color:'white',textAlign:'center',fontWeight:'bold',fontSize:'30px'}">{{currentImage}}</div>

            <div class="mt-3" :style="{display:'flex',justifyContent:'center',alignItems:'center'}">
                    <v-btn fab @click="previousImage()" small class="mx-1"><Icon :icon="['fa','chevron-left']"/></v-btn>
                    <v-btn fab @click="nextImage()" small class="mx-1"><Icon :icon="['fa','chevron-right']"/></v-btn>
            </div>
        
        </div>

    </div>
</template>
<script>

export default {
    data(){
        return{
            menu_btn:true,
            feature_func_select:undefined,
            totalUploads:0,
            totalLabeledUploads:0,
            labels:[
                /*{name:'',function:''}*/
            ],
            featureName:undefined,
            currentImage:0,
            imageUrl:null,
            automaticLabeling:false,
            datasets:[]
        }
    },
    mounted(){
      
    },
    methods:{
        toggleMenu(){
            (this.menu_btn) ? this.menu_btn = false : this.menu_btn = true;
        },
        featureSelect(ref){
            switch(ref){
                case 1:
                    this.feature_func_select=undefined;
                    break;
                case 2:
                    this.feature_func_select="IMAGE";
                    break;
                case 3:
                    this.feature_func_select="LABEL";
                    break;
            }
        },
        removeFeature(index){
            this.labels.splice(index,1);
        },
        addFeatures(){
            this.labels.push({name:this.featureName,function:this.feature_func_select});
        },
        selectData(){
            document.getElementById("data_select").click();
        },
        selectJson(){
            document.getElementById("json").click();
        },
        saveJson(){

        },
        loadTheImages(){
            //SET THE TOTAL FILES SELECTED
            this.totalUploads = document.getElementById('data_select').files.length;
            this.previewImage(0);
        },
        previousImage(){
            if(this.currentImage!==0){
                this.currentImage=this.currentImage-1;
                this.previewImage(this.currentImage);
            }
        },
        nextImage(){
            if(this.currentImage!==(document.getElementById('data_select').files.length-1)){
                this.currentImage+=1;
                this.previewImage(this.currentImage);
            }
        },
        previewImage(index){
            const file = document.getElementById("data_select").files[index];
            const reader = new FileReader();
            const vueref = this;
            reader.onload = (e)=>{
               vueref.imageUrl= e.target.result;
            }
            reader.readAsDataURL(file);
        },
        addData(){
           
        }

    }
}
</script>

<style scoped>
#menu{
    width:70px;
    background:white;
    border-radius:25px;
    box-shadow:2px 2px 10px rgba(0,0,0,.5),-2px -2px 10px rgba(255,255,255,.6);
    display:flex;
    align-items:center;
    justify-content:center;
    flex-direction: column;
    position:fixed;
    
    top:10px;
    left:5px;
}
#body{
     background:#01579b;
     width:100%;
    height:100%;
}
#label_div{
    padding-left:6em;
    padding-top:1em;
}
</style>